import Vue from 'vue';
import Vuex from 'vuex';
import app from './module/app';
// import getters from './getter';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    iscollapse: false,
  },

  mutations: {
    changeiscollapse: (state) => {
      state.iscollapse = !state.iscollapse;
    },
  },
  modules: {
    app,
  },
});
