<template>
  <div>
    <div v-for="(item, index) in route" :key="index">
      <div v-if="item.hidden == true && item.children">
        <SidebarItem :route="item.children" :basePath="item.path"></SidebarItem>
      </div>

      <div v-if="!item.hidden || item.hidden == false">
        <el-submenu :index="resolvePath(item.path)" v-if="item.children">
          <template slot="title">
            <svg-icon :icon-class="item.meta.icon" class="svg-icon"></svg-icon>
            <span :style="isShow">{{ item.meta.title }}</span>
          </template>
          <SidebarItem :route="item.children" :basePath="item.path"></SidebarItem>
        </el-submenu>
        <el-menu-item :index="resolvePath(item.path)" v-if="!item.children">
          <svg-icon :icon-class="item.meta.icon" class="svg-icon" />
          <span slot="title" :style="isShow">{{ item.meta.title }}</span>
        </el-menu-item>
      </div>
    </div>
  </div>
</template>

<script>
import path from 'path';
import { mapState } from 'vuex';
export default {
  name: 'SidebarItem',
  props: {
    route: {
      type: Array,
    },
    basePath: {
      type: String,
      default: '',
    },
  },
  computed: {
    ...mapState(['iscollapse']),
    isShow() {
      if (this.iscollapse == true) {
        return 'margin-left:25px';
      } else {
        return '';
      }
    },
  },
  methods: {
    resolvePath(routePath) {
      return path.resolve(this.basePath, routePath);
    },
  },
};
</script>

