<template>
  <svg :class="svgClass" aria-hidden="true">
    <use :xlink:href="iconName" />
  </svg>
</template>

<script>
export default {
  name: 'SvgIcon',
  props: {
    iconClass: {
      type: String,
      default: '',
    },
    className: {
      type: String,
      default: '',
    },
  },
  computed: {
    iconName() {
      return `#icon-${this.iconClass}`;
    },
    svgClass() {
      if (this.className) {
        return `svg-icon icon-${this.iconClass}` + this.className;
      } else {
        return `svg-icon icon-${this.iconClass}`;
      }
    },
  },
};
</script>

<style scoped lang="scss">
svg {
  color: $sidebarIconColor;
}
.svg-icon {
  width: 1rem;
  height: 1rem;
}
</style>
