<template>
  <div>
    <Logo :collapse="iscollapse" />
    <div class="sidebar-menu-box">
      <el-scrollbar class="menu-scroll">
        <el-menu
          :default-active="activeMenu"
          class="sidebar-menu,el-menu-vertical-demo"
          background-color="white"
          text-color="#21252b"
          :collapse="iscollapse"
          mode="vertical"
          :collapse-transition="false"
          router
        >
          <SidebarItem :route="routes"></SidebarItem>
        </el-menu>
      </el-scrollbar>
    </div>
  </div>
</template>
<script>
import SidebarItem from './SidebarItem.vue';
import Logo from './Logo';
import { mapState } from 'vuex';
export default {
  components: {
    SidebarItem,
    Logo,
  },
  data() {
    return {
      routes: [], //路由表
    };
  },
  created() {
    this.getRoutes();
  },
  mounted() {},
  methods: {
    getRoutes() {
      // 获取所有路由表
      this.routes = this.$router.options.routes;
    },
  },
  computed: {
    ...mapState(['iscollapse']),
    activeMenu() {
      const route = this.$route;
      const { meta, path } = route;
      if (meta.activeMenu) {
        return meta.activeMenu;
      }
      return path;
    },
  },
};
</script>
<style lang="scss" scoped>
.sidebar-menu-box {
  height: calc(100% - $sidebarLogoHeight);
  overflow: hidden;
}

.menu-scroll {
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  &::-webkit-scrollbar {
    overflow: hidden;
  }
}
.el-menu {
  border-right: none !important;
}
.sidebar-menu {
  width: $sideBarWidth;
  font-size: 16px;
}
</style>
