<template>
  <el-container>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item v-for="(item, index) in breadList" :key="index" :to="{ path: '/' }">
        {{ item.meta.title }}
      </el-breadcrumb-item>
    </el-breadcrumb>
  </el-container>
</template>

<script>
export default {
  name: 'BreadCrumb',
  data() {
    return {
      breadList: [],
    };
  },
  created() {
    this.getBreadList();
  },
  watch: {
    $route() {
      this.getBreadList();
    },
  },
  computed: {},
  methods: {
    getBreadList() {
      let route = this.$route.matched;
      this.breadList = route;
    },
  },
};
</script>

